.recap_numParticipants {
  width: 550px;
  height: auto;
  margin-bottom: 20px;
}

.recap_countries {
  width: 450px;
  height: auto;
  margin-bottom: 20px;
}

.recap_presenters {
  width: 450px;
  height: auto;
  margin-bottom: 20px;
}

.recap-card-teamName {
  font: italic normal bold 35px/50px Open Sans;
  letter-spacing: 0px;
  opacity: 1;
}

.recap-card-teamPlace {
  font: normal normal normal 18px/28px Open Sans;
  letter-spacing: 0px;
  opacity: 1;
}

.recap-card-projectName {
  font: italic normal bold 30px/40px Open Sans;
  letter-spacing: 0px;
  opacity: 1;
}

.recap-card-projectDesc {
  font: normal normal normal 15px/25px Open Sans;
  letter-spacing: 0px;
  opacity: 1;
}

.recap-card-img {
  height: 300px;
  width: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: top;
}

.winner1-outterdiv {
  padding-bottom: 150px;
}
.winner2-outterdiv {
  padding-bottom: 75px;
}
.winner3-outterdiv {
  padding-bottom: 0px;
}

@media only screen and (max-width: 1420px) {
  .winner1-outterdiv {
    padding-bottom: 0px;
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .winner2-outterdiv {
    padding-bottom: 0px;
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .winner3-outterdiv {
    padding-bottom: 0px;
    width: 100%;
    display: flex;
    justify-content: center;
  }
}
@media only screen and (max-width: 1000px) {
  .recap_numParticipants {
    width: 350px;
  }
  .recap_countries {
    width: 250px;
  }
  .recap_presenters {
    width: 250px;
  }
}

@media only screen and (max-width: 540px) {
  .recap_numParticipants {
    width: 80vw;
  }
  .recap_countries {
    width: 80vw;
  }

  .recap_presenters {
    width: 80vw;
  }
}
